<template>
  <div>
    <vue-confirm-dialog></vue-confirm-dialog>

    <div class="row">
      <div class="col-md-12" ref="table">
        <v-card>
          <v-card-title>
            Records

            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>

          <v-data-table
            :headers="headers"
            :items="records"
            :search="search"
            item-key="id"
          >
            <template v-slot:item.actions="{ item }">
              <a @click="viewImage(item)" >
                <v-icon class="mr-2" color="blue darken-2">
                  fa fa-image
                </v-icon></a
              >
            </template>
          </v-data-table>
        </v-card>
      </div>
    </div>

    <hr id="formData" />
  </div>
</template>

<script>
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";

export default {
  data() {
    return {
      records: [],
      search: "",
      headers: [
        {
          text: "ID",
          align: "start",
          sortable: false,
          value: "id",
        },
        { text: "User", value: "user.name" },
        { text: "Phone", value: "user.phone" },
        { text: "Payment ID", value: "payment_id" },
        
        { text: "Created at", value: "created_at" },
        { text: "Actions", value: "actions", sortable: false },
      ],
    };
  },
 mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [
      
      { title: "  Statments",route: "statments" }
    ]);
  },
  created() {
    this.fetchRecords();
  },
  methods: {
    fetchRecords() {
      this.$ApiService
        .get("/Statments")
        .then((response) => {
          //console.log(response.data);
          this.records = response.data.data;
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },
    viewImage(item) {
      window.open(item.url);
    },
  }, // end of methods
}; //end of class
</script>
